@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700;800&display=swap);

.article--top,.article--new,.article--interesting,.article--skeleton{display:flex;flex-wrap:wrap;padding:20px 0 40px 0;grid-gap:0 30px;gap:0 30px;border-bottom:1px solid #c4c4c4}.article--top .article__heading,.article--interesting .article__heading{flex:0 0 100%;margin-bottom:15px;text-transform:uppercase;font-size:14px;color:#91a0b6}.article--top .card{flex:45% 1}.article--interesting{padding-bottom:0;border-bottom:none}.article--interesting .card{flex-grow:1;flex-shrink:0;flex-basis:calc(33.333% - 20px);margin:0 0 30px 0}.article--skeleton{border-bottom:none}@media (max-width: 1103px){.article--skeleton{display:none}}

.banner{position:relative;width:100%;margin-bottom:50px;background:linear-gradient(90deg, #cfede6 7.49%, rgba(140,222,203,0.832667) 100%);border-radius:12px}.banner .banner__content{padding:25px;line-height:48px}.banner .banner__content .banner__heading{color:#04bf94;font-weight:600;font-size:32px;margin:0}.banner .banner__content .banner__subheading{margin:0 0 10px 0;font-weight:500}.banner .banner__content .banner__button{color:#fff;padding:0;border:none;outline:none;font:inherit;cursor:pointer;line-height:normal;max-width:156px;max-height:40px;background:#04bf94;padding:8px 32px;border-radius:8px}.banner .banner__image{position:absolute;right:-15px;top:-15px}@media (max-width: 1045px){.banner .banner__content{line-height:normal}.banner .banner__content .banner__heading{font-size:24px}}@media (max-width: 1023px){.banner__image{display:none}}@media (max-width: 600px){.banner .banner__content .banner__button{max-width:100%;width:100%}}

.card{display:flex;flex-direction:column}.card .card__content--date{color:#adb8c8}.card .card__content--text{color:#78787a}.card .card__image-wrapper img{width:100%;max-width:520px;height:auto}.article--new .card{display:flex;flex-direction:row}.article--new .card .card__image-wrapper img{width:520px;object-fit:contain;align-self:flex-start}.article--new .card .card__content--date{margin-top:0}.article--new .card .card__content--heading{font-size:48px;line-height:72px;margin:0}.article--new .card .card__image-wrapper{margin-right:30px}@media (max-width: 900px){.article--new .card .card__content--heading{font-size:24px;line-height:normal}}@media (max-width: 800px){.article--new .card{flex-direction:column}.article--new .card .card__image-wrapper{margin-right:0}.article--new .card .card__image-wrapper img{width:100%;max-width:520px;height:auto}}@media (max-width: 655px){.article--interesting .card{flex:45% 1}}@media (max-width: 555px){.article--top,.article--new,.article--interesting,.article--skeleton{flex-direction:column}}

.rectangle{position:relative;margin-bottom:15px}.rectangle img{border-radius:4px}.rectangle span{position:absolute;color:#fff;font-size:14px;left:21px;top:8px}

.breadcrumbs{display:flex;flex-direction:row;color:#91a0b6;margin:35px 0;padding:0}.breadcrumbs .breadcrumbs__item a{margin-right:50px}.breadcrumbs .breadcrumbs__item a::after{height:20px;position:absolute;content:'';margin-left:15px;width:24px;height:24px;background-image:url(/plantIn-website/static/media/chevron_left.57bb3d6a.svg)}@media (max-width: 1025px){.breadcrumbs{margin:15px 0}.breadcrumbs__item--last{display:none}}

.search-section{display:flex;justify-content:space-between;max-height:267px;background:rgba(4,191,148,0.16);border-radius:16px}.search__heading{padding:55px}.search__heading .search__title,.search__heading .search__subtitle{margin:5px 0}.search__heading .search__title{font-size:28px;color:#04bf94}.search-input__wrapper{position:relative;background:#ffffff;box-shadow:0px 0.47043px 2.21381px rgba(0,0,0,0.0196802);margin-top:15px;border-radius:12px;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.search-input__wrapper .search-input{border-radius:12px;outline:none;border:none;width:400px;height:56px;padding:16px;color:#91a0b6;font-size:16px}.search-input__wrapper .search-input__button{padding:0;border:none;outline:none;font:inherit;cursor:pointer;background:none;padding-right:20px;display:inline-block;vertical-align:middle}.search__image{padding-top:8px}[type='search']::-webkit-search-cancel-button,[type='search']::-webkit-search-decoration{-webkit-appearance:none;appearance:none}@media (max-width: 1045px){.search__heading{padding:25px}.search__heading .search__title{font-size:24px}}@media (max-width: 1023px){.search__image{display:none}}@media (max-width: 703px){.search-input__wrapper .search-input{width:auto;height:auto}.search-input__wrapper .search-input__button{padding-right:10px}.search__heading{padding:15px}}@media (max-width: 600px){.search-input__wrapper{width:100%}.search-input__wrapper .search-input__button{position:absolute;right:5px;top:15px}}@media (max-width: 481px){.search__heading .search__title{font-size:24px}}

.container{display:flex;flex-direction:column;max-width:1104px;padding:0 15px;margin-left:85px}@media (max-width: 1200px){.container{margin-left:0}}

.footer{display:flex;justify-content:space-between;color:#91a0b6;font-size:14px;margin:25px 0;max-width:1104px;padding:0 15px;margin-left:85px;margin-top:auto}.footer__navbar .footer__navbar-list{display:flex;padding:0}.footer__navbar .footer__navbar-list .footer__navbar-item:not(:last-child){margin-right:25px}.footer__navbar .footer__navbar-list .footer__navbar-item:not(:last-child)::after{content:'|';margin-left:25px}@media (max-width: 1200px){.footer{margin-left:0}}@media (max-width: 822px){.footer{flex-direction:column;text-align:center}.footer .footer__navbar .footer__navbar-list .footer__navbar-item:not(:last-child)::after{content:none}.footer .footer__navbar .footer__navbar-list .footer__navbar-item:not(:last-child){margin-right:0;margin-bottom:10px}.footer .footer__navbar .footer__navbar-list{flex-direction:column}}

.header{display:flex;position:relative;flex-direction:row;align-items:center;height:64px;padding:0 25px;background:#ffffff;box-shadow:0px 2px 40px rgba(0,0,0,0.1),0px 0.4px 6.5px rgba(0,0,0,0.05)}.logo{display:flex}.logo .logo__img{object-fit:contain}.navbar__list .navbar__item a{margin:0px 24px}.actions{display:flex;margin-left:auto}.actions .search-input__wrapper{position:relative;margin:5px 0}.actions .search-input__wrapper .search-input{width:auto;padding:0}.actions__item--upgrade,.actions__item--search,.actions__item--user,.actions__item--menu{padding:0;border:none;outline:none;font:inherit;cursor:pointer;background:none;margin-left:25px;display:inline-block;vertical-align:middle}.actions__item--upgrade{color:#04bf94;font-weight:600;text-shadow:0px 4px 4px rgba(0,0,0,0.25);margin-right:5px}.actions__item--menu{display:none}@media (max-width: 1130px){.navbar__list .navbar__item a{margin:0 10px}}@media (max-width: 1025px){.navbar,.actions__item--upgrade,.actions__item--user{display:none}.actions__item--menu{display:block}.navbar--active{display:block;position:absolute;left:0;top:50px;background:#fff;width:100%;text-align:center;z-index:2}.navbar--active .navbar__list{padding:0;margin:0}.navbar--active .navbar__list .navbar__item{display:flex;flex-direction:column}.navbar--active .navbar__list .navbar__item a{margin-bottom:10px}}@media (max-width: 481px){.actions__item--upgrade{display:none}}

